  margin-top: 0px;
}
.bg-purple{
  --bs-bg-opacity: 1;
  background-color: purple;
}
/*Loading Animation*/
.loader{
position: absolute;
top: 50%;
left: 50%;
transform: translate(-50%, -50%);
display: flex;
align-items: center;
  
}
/* Creating the dots */
.load{
height: 25px;
width: 25px;
margin-right: 10px;
border-radius: 50%;
background-color: green;
animation: loading 1s linear infinite;
}
/* Creating the loading animation*/
@keyframes loading {
0%{
  transform: translateX(0);
}
25%{
  transform: translateX(15px);
}
50%{
  transform: translateX(-15px);
}
100%{
  transform: translateX(0);
}
  
}
.load:nth-child(1){
animation-delay: 0.1s;
}
.load:nth-child(2){
animation-delay: 0.2s;
}
.load:nth-child(3){
animation-delay: 0.3s;
}
.load:nth-child(4){
animation-delay: 0.4s;
}
.load:nth-child(5){
animation-delay: 0.5s;
}
/*Loading Animation*/

